<template>
  <div class="information-wrap">
    <div>
      <!--      <el-input @input.native="limitInput($event, info.readNumber, 3, 2)" v-model.trim="info.readNumber"></el-input>-->
    </div>
    <br>
    <div>
      <el-form :inline="true">
        <el-form-item label=":">
          <el-input v-model.trim="condition.title" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="mg-l20" label=":">
          <el-date-picker
                  class="w300"
                  v-model.trim="condition.timeRang"
                  type="daterange"
                  value-format="timestamp"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item class="mg-l20">
          <el-button @click="Mixin_handleCurrentChange(1)"></el-button>
          <el-button type="primary" @click="clickAddBtn"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div>
      <el-table
              border=""
              :data="tableData"
              style="width: 100%">
        <el-table-column
                type="index"
                label=""
                width="50">
        </el-table-column>
        <el-table-column
                prop="id"
                label="ID">
        </el-table-column>
        <el-table-column
                prop="title"
                label="">
        </el-table-column>
        <el-table-column
                width="120"
                prop="type"
                label="">
        </el-table-column>
        <el-table-column
                width="260"
                label="">
          <template slot-scope="scope">
            <div class="thumbnail-wrap">
              <img class="hg100 wd100" :src="scope.row.imgUrl" alt="">
            </div>
          </template>
        </el-table-column>
        <el-table-column
                width="180"
                label="">
          <template slot-scope="scope">
            {{scope.row.createDate | Filter_FormatDate}}
          </template>
        </el-table-column>
        <el-table-column
                prop="showFlag"
                width="100"
                label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.showFlag" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column
                width="300"
                label="">
          <template slot-scope="scope">
            <el-button :loading="scope.row.buttonLoading" @click="clickEditBtn(scope.row)" type="primary"></el-button>
            <el-button v-if="!scope.row.showFlag" @click="clickStartOrEndBtn(scope.row, 'start')" type="success">
            </el-button>
            <el-button v-if="scope.row.showFlag" @click="clickStartOrEndBtn(scope.row, 'end')" type="warning">
            </el-button>
            <el-button type="danger" @click="clickRemoveBtn(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row class="mg-t20">
        <el-col :offset="8" :span="8">
          <el-pagination
                  @current-change="Mixin_handleCurrentChange"
                  :page-size="Mixin_pageSize"
                  layout="total, prev, pager, next, jumper"
                  :total="Mixin_total">
          </el-pagination>
        </el-col>
      </el-row>
    </div>

    <el-dialog
            :title="currentHandle === 'add' ? '': ''"
            :visible.sync="isShowInfoDialog"
            @close="Mixin_dialogClose('info', 'isShowInfoDialog')"
            width="1000px">
      <el-form ref="info" :model="info" :rules="infoRules" label-width="120px">
        <el-form-item label=" :" prop="title">
          <el-input maxlength="20" placeholder="" v-model.trim="info.title"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="img">
          <CmUpload
                  upload-name="img"
                  :initObj="info.img"
                  @uploadSuccess="uploadSuccess">
          </CmUpload>
        </el-form-item>

        <el-form-item label=" :" prop="praisePoints">
          <el-input maxlength="100" placeholder="" v-model.trim="info.praisePoints"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="readNumber">
          <el-input maxlength="100" placeholder="" v-model.trim="info.readNumber"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="type">
          <el-select class="wd100" v-model="info.type" placeholder="">
            <el-option :label="1" :value="1"></el-option>
            <el-option :label="2" :value="2"></el-option>
            <el-option :label="3" :value="3"></el-option>
            <el-option :label="4" :value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=" :" prop="details">
          <editor-bar v-model="info.details" :isClear="isClear"></editor-bar>
        </el-form-item>

      </el-form>
      <div class="mt10 dis-fl ju-ct">
        <el-button :loading="submitButtonLoading" type="primary" @click="submitInfoBtn('info')"></el-button>
        <el-button @click="isShowInfoDialog = false"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  import EditorBar from '../../components/editor'

  export default {
    name: "information",
    data() {
      return {
        //
        condition: {
          title: '',
          timeRang: null
        },
        //
        tableData: [],
        //
        isShowInfoDialog: false,
        //obj
        info: {
          //id
          id: '',
          //
          title: '',
          //
          details: '',
          //
          praisePoints: '',
          //
          readNumber: '',
          //
          type: '',
          //
          img: ''
        },
        //
        infoRules: {
          title: [
            {
              required: true,
              validator: this.$verifys.nullStr({item: ''}),
              trigger: 'blur'
            },
          ],
          details: [
            {
              required: true,
              validator: this.$verifys.nullStr({item: ''}),
              trigger: 'blur'
            },
          ],
          praisePoints: [
            {
              required: true,
              validator: this.$verifys.nullStr({item: ''}),
              trigger: 'blur'
            },
          ],
          type: [
            {
              required: true,
              validator: this.$verifys.nullStr({item: ''}),
              trigger: 'change'
            },
          ],
          readNumber: [
            {
              required: true,
              validator: this.$verifys.nullStr({item: ''}),
              trigger: 'blur'
            },
          ],
          img: [
            {
              required: true,
              validator: this.$verifys.nullStr({item: ''}),
              trigger: 'change'
            },
          ],
        },
        //(edit->, add->)
        currentHandle: '',
        submitButtonLoading: false,
        //
        isClear: false,
        detail: ""

      }
    },
    computed: {},
    created() {
    },
    mounted() {
      this.$nextTick(() => {
        this.initData();
      })
    },
    methods: {
      //
      initData() {
        let params = {
          title: this.condition.title,
          currentPage: this.Mixin_currentPage,
          pageSize: this.Mixin_pageSize,
        };
        if (this.condition.timeRang) {
          params.startDate = this.condition.timeRang[0];
          params.stopDate = this.condition.timeRang[1] + (1000 * 60 * 60 * 24 - 1);
        }
        this.$api.information.getInfoList(params).then(res => {
          if (res.data && res.data.resultCode === 0) {
            res.data.data.records.forEach((item, index) => {
              item.buttonLoading = false;
            });
            this.tableData = res.data.data.records;
            this.Mixin_total = res.data.data.total;
          }
        });
      },
      //
      clickAddBtn() {
        this.currentHandle = 'add';
        this.info.title = '';
        this.info.details = '';
        this.info.praisePoints = '';
        this.info.readNumber = '';
        this.info.type = '';
        this.info.img = '';
        this.isShowInfoDialog = true;
      },
      //
      clickEditBtn(row) {
        this.currentHandle = 'edit';
        this.info.id = row.id;
        row.buttonLoading = true;
        this.$api.information.getInformationById(row.id).then(res => {
          row.buttonLoading = false;
          if (res.data && res.data.resultCode === 0) {
            let data = res.data.data;
            this.isShowInfoDialog = true;
            this.info.title = data.title;
            this.info.details = data.context;
            this.info.praisePoints = data.praisePoints;
            this.info.readNumber = data.readNumber;
            this.info.type = data.type;
            this.info.img = data.imgUrl;
          }
        });
      },
      //
      uploadSuccess(data) {
        this.info.img = data.imgSrc;
      },
      //
      submitInfoBtn(formName) {
        this.$refs[formName].validate(async valid => {
          if (valid) {
            this.submitButtonLoading = true;
            if (this.currentHandle === 'add') {
              //
              let params = {
                context: this.info.details,
                title: this.info.title,
                praisePoints: this.info.praisePoints,
                readNumber: this.info.readNumber,
                type: this.info.type,
                showFlag: false,
                imgUrl: this.info.img,
              };
              this.$api.information.addInformation(params).then(res => {
                this.submitButtonLoading = false;
                if (res.data && res.data.resultCode === 0) {
                  this.$message.success('');
                  this.initData();
                  this.isShowInfoDialog = false;
                }
              });
            } else {
              //
              let params = {
                id: this.info.id,
                context: this.info.details,
                title: this.info.title,
                praisePoints: this.info.praisePoints,
                readNumber: this.info.readNumber,
                type: this.info.type,
                showFlag: false,
                imgUrl: this.info.img,
              };
              this.$api.information.addInformation(params).then(res => {
                this.submitButtonLoading = false;
                if (res.data && res.data.resultCode === 0) {
                  this.$message.success('');
                  this.initData();
                  this.isShowInfoDialog = false;
                }
              });
            }
          }
        })
      },
      ///
      clickStartOrEndBtn(row, type) {
        this.$confirm(`${type === 'start' ? '' : ''} ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$api.information.updateShow(row.id).then(res => {
            if (res.data && res.data.resultCode === 0) {
              this.$message.success(`${type === 'start' ? '' : ''}`);
              this.initData();
            }
          });
        }).catch(() => {
        });
      },
      //''
      clickRemoveBtn(row) {
        this.$confirm(` ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$api.information.delInformation(row.id).then(res => {
            if (res.data && res.data.resultCode === 0) {
              this.$message.success(``);
              this.Mixin_handleCurrentChange(1);
            }
          });
        }).catch(() => {
        });
      },
      //
      limitInput(event, obj, before, after) {
        let value = event.target.value;
        value = value.replace(/[^ \d.]|\s|^\./g, "");
        value = value.replace(/\.{2,}/g, ".");
        value = value.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
        console.log(value.split("."));
        if (after) {
          value = value.split(".")[0].slice(0, before) + "." + value.split(".")[1] ? value.split(".")[1].slice(0, after) : '';
        } else {
          value = value.slice(0, before);
        }
        event.target.value = value;

        obj = event.target.value;
        console.log(obj);
      },
    },
    props: {},
    watch: {},
    mixins: [],
    filters: {},
    components: {EditorBar},
    directives: {},
  }
</script>

<style lang="scss" scoped>

</style>
